
html,
body {
  margin: 0;
  padding: 0;
}
.title {
  font-size: 40px;
  text-align: center;
  margin: 20px;
}
.stage {
  margin: auto;
  width: 1200px;
  height: 800px;
  background: #cad7dd;
  border: solid 2px #000;
}
.login {
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: rgba(0, 0, 0, 0.479);
}
.login__box {
  width: 400px;
  height: 100px;
  background: #fff;
  border: solid 3px #000;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 26px;
}
